.messenger {
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 150px;
  right: 40px;
  border-radius: 50%;
  background-color: var(--dark-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.messenger:hover {
  cursor: pointer;
}
.messenger i {
  color: white;
}
